<template>
    <div class="my-popups modal right fade show" style="display:block" role="dialog">
        <div class="modal d-block " tabindex="-1" role="dialog">
            <div class="modal-dialog doc-setup-slide">
                <div class="modal-content">
                    <div class="modal-header mheader-user py-0">
                        <button type="button" class="close" v-on:click="$emit('Close')">
                            <em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                        <h4 class="modal-title py-3" v-if="popupCondition=='setup'">{{ $t('SetupCRM') }}</h4>
                        <h4 class="modal-title py-3" v-if="popupCondition=='configuration'">{{ $t('Configuration') }}</h4>
                        <h4 class="modal-title py-3" v-if="popupCondition=='GeneralSetting'">{{ $t('GeneralSetting') }}</h4>
                        <h4 class="modal-title py-3" v-if="popupCondition=='EnableDisbleSection'">{{ $t('EnableDisble') }}</h4>
                    </div>
                    <div class="modal-body bg-white">
                        <section class="container-fluid p-0">
                            <div class="left-menu-tab new_tab">
                                <div class="col-md-12 pb-0" v-if="popupCondition == 'setup'">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                            <div class="row">
                                                <div class="col-8 pr-0 font-weight-medium">{{ $t('CompleteYourProfile') }}
                                                </div>
                                                <div class="col-4 pl-0 text-right"><small>
                                                        <em class="fa fa-check-circle pr-1"
                                                            v-bind:class="{ 'text-success': SetupProgress == 100, 'text-muted': SetupProgress == 0, 'text-warning': SetupProgress == 50}"></em></small>
                                                </div>
                                            </div>
                                            <div class="progress smaller my-2">
                                                <div
                                                    class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused"
                                                    id="divConsumendStorage" :class="`${progressBarColor}`"
                                                    style="font-weight:500; font-size:14px"
                                                    :style="{width:SetupProgress==''?6:SetupProgress + '%'}"
                                                    :title="SetupProgress">{{ SetupProgress==""?0:SetupProgress }}%</div>
                                            </div>
                                            <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                                                <ul>
                                                    <li>
                                                        <b id="divConsumendStorage"><span>Progress {{ CurrentTab }} of
                                                                {{ TotalStep }}</span></b>
                                                    </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);"
                                    v-if="popupCondition == 'setup'">
                                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des border">
                                        <li @click="ShowNextTab(1)" class="nav-item FirstScreen">
                                            <a class="nav-link left-menu" v-bind:class="{ 'active': CurrentTab == 1 }"
                                                style="cursor: pointer;"><em
                                                    class="fa fa-user action-icon pr-2 text-muted"></em>{{ $t('EnterLead')
                                                    }}</a>
                                        </li>
                                        <li @click="ShowNextTab(2)" class="nav-item ThirdScreen">
                                            <a class="nav-link left-menu" v-bind:class="{ 'active': CurrentTab == 2 }"
                                                style="cursor: pointer;"><em
                                                    class="fa fa-cogs text-info indl font-18 pr-2 text-muted"></em>{{
                                                        $t('Configuration') }}</a>
                                        </li>
                                    </ul>
                                </div>
                                <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);"
                                    v-if="popupCondition == 'configuration'">
                                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des border">
                                        <li @click="ShowNextTab(1)" class="nav-item FirstScreen">
                                            <a class="nav-link left-menu" v-bind:class="{ 'active': CurrentTab == 1 }"
                                                style="cursor: pointer;"><em class="fa fa-cog action-icon pr-2"></em>
                                                {{ $t('General') }}
                                            </a>
                                        </li>
                                        <li @click="ShowNextTab(2)" class="nav-item FirstScreen">
                                            <a class="nav-link left-menu" v-bind:class="{ 'active': CurrentTab == 2 }"
                                                style="cursor: pointer;"><em class="fa fa-users action-icon pr-2"></em>
                                                {{ $t('Client') }}
                                            </a>
                                        </li>
                                        <li @click="ShowNextTab(3)" class="nav-item FirstScreen">
                                            <a class="nav-link left-menu" v-bind:class="{ 'active': CurrentTab == 3 }"
                                                style="cursor: pointer;"><em class="fa fa-tasks action-icon pr-2"></em>
                                                {{ $t('CRM') }}
                                            </a>
                                        </li>
                                        <li @click="ShowNextTab(4)" class="nav-item FirstScreen">
                                            <a class="nav-link left-menu" v-bind:class="{ 'active': CurrentTab == 4 }"
                                                style="cursor: pointer;"><em class="fa fa-envelope action-icon pr-2"></em>
                                                {{ $t('Email') }}
                                            </a>
                                        </li>
                                        <li @click="ShowNextTab(5)" class="nav-item FirstScreen">
                                            <a class="nav-link left-menu" v-bind:class="{ 'active': CurrentTab == 5 }"
                                                style="cursor: pointer;"><em class="fa fa-building action-icon pr-2"></em>
                                                {{ $t('ProjectManagement') }}
                                            </a>
                                        </li>
                                        <li @click="ShowNextTab(6)" class="nav-item FirstScreen">
                                            <a class="nav-link left-menu" v-bind:class="{ 'active': CurrentTab == 6 }"
                                                style="cursor: pointer;"><em class="fa fa-file-text action-icon pr-2"></em>
                                                {{ $t('Quote') }}
                                            </a>
                                        </li>
                                    </ul>
                                </div>                                
                            </div>
                            <div class="right-content">
                                <div class="col-md-12 p-0" v-if="popupCondition == 'setup'">
                                    <AddLeadVue v-if="CurrentTab == 1" :ModuleId="ModuleId"></AddLeadVue>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition == 'setup'">
                                    <ConfigurationTab v-if="CurrentTab == 2" :ModuleId="ModuleId"></ConfigurationTab>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition == 'configuration'">
                                    <General v-if="CurrentTab == 1"></General>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition == 'configuration'">
                                    <Client v-if="CurrentTab == 2" :ModuleId="ModuleId"></Client>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition == 'configuration'">
                                    <Configuration v-if="CurrentTab == 3" :ModuleId="ModuleId"></Configuration>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition == 'configuration'">
                                    <Email v-if="CurrentTab == 4" :ModuleId="EmailModuleId"></Email>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition == 'configuration'">
                                    <ProductMangement v-if="CurrentTab == 5" :ModuleId="ProjectModuleId"></ProductMangement>
                                </div>
                                <div class="col-md-12 p-0" v-if="popupCondition == 'configuration'">
                                    <Qoute v-if="CurrentTab == 6" :ModuleId="QuotationModuleId"></Qoute>
                                </div>                                                             
                            </div>
                        </section>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService';
import AddLeadVue from './AddLead.vue'
import ConfigurationTab from './ConfigurationTab.vue';
import Configuration from './Configuration.vue';
import General from './General.vue';
import Client from './Client.vue'
import Email from './Email.vue'
import ProductMangement from './ProductMangement.vue'
import Qoute from './Qoute.vue'
export default {
    components: {
        AddLeadVue,
        ConfigurationTab,
        Configuration,
        General,
        Client,
        ProductMangement,
        Qoute,
        Email
    },
    props: {
        popupCondition: {
            type: String,
            required: true
        },
    },
    data() {
        return {
            CurrentTab: 1,
            ModuleData: [],
            ModuleId: 0,
            EmailModuleId:0,
            ProjectModuleId:0,
            QuotationModuleId:0,
            SetupProgress: 0,
            CompanyType: 'normal',
            TotalStep: 2,
            progressBarColor: ''
        }
    },    
    created: async function () {        
        var vm = this;
        await vm.GetModule();
        await vm.GetProgresSetupStatusDetail();
    },
    methods: {
        ShowNextTab: function (dta) {
            var vm = this
            vm.CurrentTab = dta;
        },        
        GetProgresSetupStatusDetail: async function () {
            var vm = this;
            let SetupProgres=0;
            var url = `companyType=${vm.CompanyType}`
            await DataService.GetProgresSetupStatusDetail(url).then(function (response) {
                if (response.data.result.length > 0) {
                    response.data.result.forEach(element => {
                        if (element.PROGRESS != null) {
                            SetupProgres = SetupProgres + element.PROGRESS;
                        }
                    });
                    vm.SetupProgress = SetupProgres;
                    if (vm.SetupProgress == 50) {
                        vm.progressBarColor = 'progress-bar progress-bar-striped bg-warning';
                    } else {
                        vm.progressBarColor = 'progress-bar progress-bar-striped bg-success';
                    }
                }                
            });
        },
        GetModule: async function () {
            var vm = this;
            await DataService.GetAllModule().then(response => {                            
                vm.ModuleData = response.data;                
                vm.ModuleData.forEach((itm, index) => {                                        
                    if (itm.moduleName == 'CRM') {
                        vm.ModuleId = itm.moduleId;
                    }
                    if (itm.moduleName == "Email") {
                        vm.EmailModuleId = itm.moduleId;
                    }
                    if (itm.moduleName == "Quotation") {
                        vm.QuotationModuleId = itm.moduleId;
                    }
                    if (itm.moduleName == "Asset") {
                        vm.ProjectModuleId = itm.moduleId;
                    }
                })
            })
        }
    }
}
</script>
<style>
.newtab_des .nav-link.left-menu.active {
    background: #e0f2ff !important;
}
</style>
