<template>
    <section>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <div id="step1" class="tab-pane call-detail fade p-0 border pb-3 active show">
                    <section class="page-action w-100">
                        <div class="container-fluid p-0">
                            <div class="col-md-12 p-0 mt-3">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control float-left" id="ulKeysContainer">
                                    <dynamic-form lang="en" ref="AddLead" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                                    <div class="col-lg-6">
                       <div class="py-2"><small><span class="text-danger">
                                   {{ $t('MandatoryString') }}</span></small>
                       </div>
                       </div>
                                </div>                               
                            </div>                            
                        </div>                        
                    </section>                    
                </div>
            </div>
        </div>        
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
import SearchSechma from '../../CRM/AdvancedSetup/SearchSchema';
export default{
  created:function(){
    this.username = this.GetUserInfo.Name;
    this.userid = this.GetUserInfo.ID;
    this.GetCountryData();
    this.GetLeadOwner();
  },
  data(){
    return{
        LeadData:[],
        username: null,
        userid: null,
        isLoading:false,
        FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FirstName'),
                            name: "FirstName",
                            value: "",
                            placeholder: this.$t('FirstName'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LastName'),
                            name: "LastName",
                            value: "",
                            placeholder: this.$t('LastName'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LeadEmail'),
                            name: "LeadEmail",
                            value: "",
                            placeholder: this.$t('LeadEmail'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('LeadOwner'),
                            name: "LeadOwner",
                            value: "",
                            placeholder: this.$t('LeadOwner'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('LeadDate'),
                            name: "LeadDate",
                            value: "",
                            placeholder: this.$t('LeadDate'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: this.$t('Status'),
                            config: {
                                options: [{name:'Active',value:1001},{name:'InActive',value:'1002'}],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            placeholder: this.$t('Country'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                    ]
                }
            ],
            buttons: [
                   {
                       type: "submit",
                       class: "btn btn-success",
                       text: "<i class='fa fa-save pr-2'></i>Save",
                   }
               ],
    }
  },
  methods:{
    GetCountryData:function(){
        var vm=this;
        DataService.GetCountryData().then(function(response) { 
            var data=response.data;
            data.forEach(element => {
                vm.FormSchema[0].Data[6].config.options.push({
                    name:element.countrY_FULL_NAME,
                    value:element.id
                })
            });
            vm.$refs.AddLead.UpdateKeyValue();
        })
    },
    GetLeadOwner:function(){
        var vm=this;
        vm.FormSchema[0].Data[3].config.options.push({
            name:vm.username,
            value:vm.userid
        })
    },
    onSubmit:function(formfields){      
        var vm=this; 
        vm.isLoading=true;       
        var obj={
                    "first_name": formfields.FirstName,
                    "last_name": formfields.LastName,
                    "email": formfields.LeadEmail,
                    "lead_owner": formfields.LeadOwner,
                    "lead_date": formfields.LeadDate,
                    "status_id": formfields.Status,
                    "country": formfields.Country
                }
        var params=obj;             
        DataService.AddLead(params).then(function(response) { 
            if (response != null && response.data[0].Status == "Success") {                
                vm.ShowAlert(vm.$t("RecordsSavedSuccessfully"));
                SearchSechma.GetProgresSetupStatusDetail(this, "Lead");
                vm.$parent.GetProgresSetupStatusDetail();

              }
        })
        vm.isLoading=false;
    }
  }
}
</script>
