<template>
    <section class="page-action">
        <div class="container-fluid mb-4 p-0">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition p-actions-expand tablehead">
                        <span class="p-name text-white">{{ $t('AdvanceSetup') }}</span>
                        <span class="p-actions float-right">
                            <RouterLink :to="'/CRM/Index'" title="" id="backtolist"
                                class="p-action-btn text-white float-right" :data-original-title="$t('BacktoList')">
                                <em class="fa fa-angle-double-left pr-2"></em><br />{{ $t('BacktoList') }}
                            </RouterLink>
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="row">
            <div class="col-12 col-md-10 mx-auto">
                <div class="row">
                    <div class="col-md-12 padding-t_8">
                        <div class="top-search">
                            <h5 class="font-weight-normal text-center my-4">{{ $t('HeadingText') }}</h5>
                            <div class="row pt-2 p-0 mx-auto justify-center">
                                <div class="col-sm-12 col-md-8 col-lg-4 col-xl-4 float-left Search-panel float-left">
                                    <div class="row mt-0">
                                        <div class="col-10 float-left pr-1 pl-0">
                                            <div class="form-group"><input 
                                                    placeholder="Search by Name, Module" type="text" autocomplete="on"
                                                    class="form-control" v-model="searchValue"></div>
                                        </div>
                                        <div class="col-2 float-left pl-0">
                                            <div class="background-0 text-left form-check-inline"><a @click="searchResult"
                                                    data-toggle-tooltip="tooltip" data-original-title="Search"
                                                    class="btn btn-success mr-1 px-4"><em class="fa fa-search"></em></a><a
                                                    id="Clear" href="javascript:;" title="" data-toggle-tooltip="tooltip"
                                                    data-original-title="Clear" @click="clearResult"
                                                    class="btn btn-danger clearsearchtext px-4"><em
                                                        class="fa fa-refresh"></em></a></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row flex-row"> 
                    <div class="col-md-12 col-lg-6" v-for="(itm, index) in SectionData" :key="index">
                            
                        <a onmousedown="return false;" href="javascript:void(0)"
                            class="doc-setup short-menu-item bg-white px-0 py-4 my-2 position-relative"
                            data-toggle-tooltip="tooltip" data-original-title="Setup CRM"
                            @click="ShowSetCRMPopup(itm.name)" :class="`${itm.divclass}`">                            
                            <div class="col-md-9 col-lg-8 col-xl-9 col-sm-9 col-9">
                                <div class="px-0" id="Userlist">
                                    <h5 class="card-title font-16 mb-0">{{ $t(itm.htext) }}</h5>
                                    <p class="card-text font-12 pr-5">{{ $t(itm.ptext) }}</p>
                                </div>                
                                <div class="progress smaller mr-5 my-2" v-if="itm.iSetupProgress">
                                    <div class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused"
                                        id="divConsumendStorage" :class="`${itm.progressColor}`"
                                        style="font-weight:500; font-size:14px"
                                        :style="{width:itm.SetupProgress==''?6:itm.SetupProgress + '%'}"
                                        :title="itm.SetupProgress">{{ itm.SetupProgress==''?0:itm.SetupProgress }}%</div>
                                </div>
                            </div>
                            <div class="col-md-2" v-if="itm.ibarprogress">
                                <span class="barprogress">
                                    <div class="Task">
                                        <div class="c100 small" v-bind:class="[itm.progressColor, ('p' + itm.SetupProgress)]">
                                            <span>{{ itm.SetupProgress }}%</span>
                                            <div class="slice">
                                                <div class="bar"></div>
                                                <div class="fill"></div>
                                            </div>
                                        </div>

                                    </div>

                                </span>
                            </div>
                            <span class="tickarrowposition d-flex align-items-center">
                                <i class="fa fa-angle-right m-auto text-dark font-20" aria-hidden="true"></i>
                            </span>
                        </a>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-12 text-center float-left">
            <h5 class="my-4">{{ $t('MoreSettings') }}</h5>
        </div>
        <div class="col-12 col-md-10 m-auto">
            <div class="listing">
                <div class="row">
                    <h6 class="ml-3 mb-0">{{ $t('CRMSetting') }}</h6>
                </div>
                <div class="row card-view bg-border custom-box-width">
                    <div class="col-sm-12 col-lg-3 col-xl-2 col-md-12 div-cardView" v-for="(item, index) in ModuleData"
                        :key="index">
                        <div class="box docattachment py-5 border my-3" @click="RedirectToLinks(item.name)">
                            <em class="nxt-page-icon fa fa-share-square-o" role='button'></em>
                            <div class="d-flex align-items-center m-auto" :class="item.bClass">
                                <em class="font-24 m-auto text-white" aria-hidden="true" :class="item.iClass"></em>
                            </div>
                            <div class="row mx-0 align-items-end">
                                <div class="col-md-12 font-14 mt-2 text-center">{{ $t(item.name) }} </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <SetUpCrmModalPopup v-if="showSetUpCrmModalPopup" @Close="Close" :popupCondition="popupCondition">
        </SetUpCrmModalPopup>
        <EnableDisable v-if="IsEnableDisable"></EnableDisable>
    </section>
</template>
<script>
import SetUpCrmModalPopup from './SetUpCRM.vue';
import SearchSechma from '../../CRM/AdvancedSetup/SearchSchema';
import EnableDisable from './EnableDisable.vue';
export default {
    components: {
        SetUpCrmModalPopup,EnableDisable
    },
    data() {
        return {
            showSetUpCrmModalPopup: false,
            popupCondition: '',
            CompanyType: '',
            SetupProgress: 0,
            searchValue:'',
            moduleId: 0,
            progressColor: '',
            ModuleData: SearchSechma.leftSearchFields,
            SectionData: SearchSechma.ClickSection,
            IsEnableDisable:false

        }
    },
    created:async  function () {
        await SearchSechma.GetProgresSetupStatusDetail(this, "Lead");     
    },
    
    methods: {
        clearResult:function(){
            this.searchValue='';
            this.ModuleData=SearchSechma.leftSearchFields;
        },
        searchResult() {   
            if (this.searchValue != '' && this.searchValue) {
                this.ModuleData = this.ModuleData.filter((item) => {
                    return item.name.includes(this.searchValue);
                })
            }        
            return this.ModuleData;
        },        
        ShowSetCRMPopup: function (param) {            
            var vm = this;
            if(param =='GeneralSetting'){
                vm.showSetUpCrmModalPopup=false;
            }
            else if(param =='EnableDisbleSection'){
                vm.IsEnableDisable=true;
            }
            else {
                vm.showSetUpCrmModalPopup=true;
                vm.IsEnableDisable=false;
            }          
            vm.popupCondition = param
        },
        Close: function () {
            var vm = this;
            vm.showSetUpCrmModalPopup = false;
        },
        RedirectToLinks: function (redirectType) {
            var envLink = process.env;
            if (redirectType == "Feedback") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Feedback/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "IndustryType") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/IndustryType/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "ProductType") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/CRM/ItemTypeListing'
                window.open(link);
                return false
            }
            else if (redirectType == "Channel") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Channel/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "Category") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Category/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "CustomFields") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Custom/fields'
                window.open(link);
                return false
            }
            else if (redirectType == "ManageLayout") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Custom/Layouts'
                window.open(link);
                return false
            }
            else if (redirectType == "EmailTemplate") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/EmailTemplate/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "Clients") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Clients/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "ManageTax") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Tax/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "Status") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Status/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "CRMRoleType") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Roles/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "CurrencyManagement") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/CurrencyManagement/Index'
                window.open(link);
                return false
            }
            else if (redirectType == "ContactType") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/ContactType/Index'
                window.open(link);
                return false
            }

        }
    }
}
</script>
<style>
.barprogress .bg-success .fill, .barprogress .bg-success .bar {
    border-color: #28a745;
}
.c100 {
    width: 1.4em;
    height: 1.4em;
   
}
.c100:after {
     top: 0.10em;
    left: 0.10em;
   
    width: 1.2em;
    height: 1.2em;
}
.c100.progress-bar-striped .bar, .c100.progress-bar-striped .fill {
    border: none;
}
.c100 > span {
     line-height: 69px;
    font-weight: 600;
    font-size: 14px;
}
.c100.bg-warning {
    background: #cccccc !important;
}
.c100.bg-warning .bar {
    background: #ffc107;
    border-color: #ffc107;
    clip: auto;
    width: 100%;
    transform: rotate(282deg);
    height: 99%;
    left: 15px;
    top: 0px;
}
.c100.bg-warning .slice {
    clip: auto;
}
.c100 .slice {
    width: 1.4em;
    height: 1.4em;
    clip: rect(0em, 1em, 1em, 0.5em);
}

.progress {
    display: -ms-flexbox;
    display: flex;
    height: 11px;
    overflow: hidden;
    line-height: 0;
    font-size: .75rem;
    background-color: #e9ecef;
    border-radius: .25rem;
}

.row .flex-row{
    display: flex;
    align-items: center;
}

a.doc-setup.box-padding {
    margin: 0px !important;
    padding: 12px 0px !important;
}

div#divConsumendStorage {
    font-weight: 500;
    font-size: 11px !important;
    padding: 2px !important;
}

</style>