<template>
  <section>
    <div class="col-md-12 p-0">
            <div class="tab-content">
                <div id="step1" class="tab-pane call-detail fade p-0 border pb-3 active show">
                    <section class="page-action w-100">
                        <div class="container-fluid p-0">
                            <div class="col-md-12 p-0 mt-3">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control float-left"
                                    id="ulKeysContainer">
                                    <div class="container-fluid p-0">
                                        <div class="col-md-12 p-0">
                                            <div class="row">                                           
                                                <Form v-slot="{ values, errors }" class="col-12 p-0" ref="form">
                                                    <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6"                                                        
                                                        v-for="(item, index) in allConfigData">
                                                        <div v-if="item.CORE_CONFIG_TYPE == 'text'"
                                                            class="form-group projectConfig">
                                                            <label>
                                                                <span> {{ $t(item.CORE_CONFIG_KEY) }} </span>
                                                                <span class="text-danger">*</span>
                                                                <a href="javascript:;" :data-content="item.CORE_CONFIG_DESC"
                                                                    data-toggle="popoveruserguide">
                                                                    <i class="fa fa-question-circle-o text-popover"
                                                                        style="font-size: 14px"></i>
                                                                </a>
                                                            </label>                                                        
                                                            <div class="input-group">
                                                            <input :keys="item.CORE_CONFIG_ID" type="text"
                                                                :name="item.CORE_CONFIG_DESC"
                                                                :value="item.CORE_CONFIG_VALUE"
                                                                class="textfield form-control" />
                                                            
                                                        </div>                                                          
                                                        </div>
                                                        <div v-if="item.CORE_CONFIG_TYPE == 'dropdown'"
                                                            class="form-group projectConfig">
                                                            <label>
                                                                <span> {{ $t(item.CORE_CONFIG_KEY) }}</span>
                                                                <span class="text-danger">*</span>
                                                                <a href="javascript:;" data-toggle="popoveruserguide"
                                                                    :data-content="item.CORE_CONFIG_DESC">
                                                                    <i class="fa fa-question-circle-o text-popover"></i>
                                                                </a>
                                                            </label>
                                                            <Field :rules="{ 'required': true }"
                                                                name="item.CORE_CONFIG_DESC" v-slot="{ errors }">
                                                                <select :keys="item.CORE_CONFIG_ID"                                                                  
                                                                    class="select form-control"
                                                                    v-model="item.CORE_CONFIG_VALUE"
                                                                    name="EnableLocationTrackingModule">
                                                                    <option
                                                                        v-for="(items, GetoptionIndex) in Getoption(item.CORE_CONFIG_OPTIONS)"
                                                                        :value="items" :key="GetoptionIndex">
                                                                        {{ items }}
                                                                    </option>
                                                                </select>
                                                                <span name="EnableLocationTrackingModule"
                                                                    id="EnableLocationTrackingModule"
                                                                    class="invalid-feedback d-block">{{ errors[0]
                                                                    }}</span>
                                                            </Field>
                                                        </div>
                                                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6"
                                                            id="radios" type="radiobutton">
                                                            <label v-if="item.CORE_CONFIG_TYPE == 'radiobutton'">
                                                                <span> {{ $t(item.CORE_CONFIG_KEY) }} </span>
                                                                <span class="text-danger">*</span>
                                                            </label>
                                                            <div class="rad_value" type="radiobutton"
                                                                :keys="item.CORE_CONFIG_ID"
                                                                v-if="item.CORE_CONFIG_TYPE == 'radiobutton'">
                                                                <label
                                                                    v-for="(items, indx) in Getoption(item.CORE_CONFIG_OPTIONS)"
                                                                    class="cogfiglbl form-check form-check-inline">
                                                                    <div
                                                                        class="custom-control custom-radio custom-control-inline">
                                                                        <input type="radio" class="custom-control-input"
                                                                            :name="item.CORE_CONFIG_DESC"
                                                                            :id="'rdo_' + indx + index"
                                                                            v-model="item.CORE_CONFIG_VALUE"
                                                                            :value="items" />
                                                                        <label class="custom-control-label"
                                                                            :for="'rdo_' + indx + index"></label>
                                                                    </div>{{ items == 'no' ? 'No' : 'Yes' }}
                                                                </label>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12">
                                                        <div class="row flex-row-reverse">
                                                            <div class="col-lg-6 text-right">
                                                                <a id="hlkSave" @click="SaveConfiguration(values, errors)"
                                                                    issecurityprompt="IsSecurityPrompt"
                                                                    class="btn btn-success formbtn" href="javascript:;"
                                                                    title="Save"><em class="fa fa-save pr-2"></em>Save</a>
                                                            </div>
                                                        </div>
                                                    </div>
                                        
                                                </Form>
                                            </div>
                                            <div class="col-lg-6">
                                            <div class="py-2"><span class="mandatory">{{$t('MandatoryString')}}</span></div>
                                        </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
        </div>
  </section>
</template>

<script>
import DataService from '../../../services/DataService';
export default {
    props: {
        ModuleId: {
            type: Number,
            required: true
        }
    },
    data(){
        return{            
            allConfigData: [],
        }
    },
    created:function(){        
        var vm = this;       
        vm.GetConfigurationData();
    },
    methods:{
        Getoption: function (params) {
            params = params == null ? '' : params;
            var options = [];
            $.each(params.split('^'), function (i, v) {
                options.push(v);
            });
            return options;
        },
        GetConfigurationData: function () {            
            var vm = this;
            var myObj = `moduleId=${vm.ModuleId}`
            DataService.GetConfigurationData(myObj).then(function (response) {                
                vm.allConfigData = response.data.StorageKeys;
            })
        },
        SaveConfiguration: function (values, error) {
            debugger
            var vm = this;
            vm.objData = [];
            vm.isLoading = true;
            var textValues = $('div#ulKeysContainer input:text,div#ulKeysContainer select,div#ulKeysContainer textarea,div#ulKeysContainer input:password');
            var checkBoxValues = $('div#ulKeysContainer div[type="checkbox"], div#ulKeysContainer div[type="radiobutton"]');
            for (var i = 0; i < textValues.length; i++) {
                if ($(textValues[i]).attr('keys') != undefined) {
                    if ($(textValues[i]).val() != null && $(textValues[i]).val() != undefined) {
                        var textValue = $(textValues[i]).val().trim();
                        if ($(textValues[i]).val().trim() == '') {
                            if ($(textValues[i]).attr("class").indexOf("notrequired") == -1) {
                                $(textValues[i]).addClass('is-invalid');
                                $(textValues[i] + 'span#LocationTracking').html('Required');
                            }
                        }
                        else {
                            $(textValues[i]).removeClass('is-invalid');
                        }
                        let objStorageConfigData = {
                            DataId: $(textValues[i]).attr('keys'),
                            DataValue: textValue
                        }
                        vm.objData.push(objStorageConfigData);
                    }
                }
            }
            for (var i = 0; i < checkBoxValues.length; i++) {
                var getSelectedvalues = '';
                if ($(checkBoxValues[i]).attr('keys') != undefined && $(checkBoxValues[i]).attr('keys') != 'undefined') {
                    if ($(checkBoxValues[i]).attr('type') == 'radiobutton') {
                        var combinedName = ("config" + i);
                        getSelectedvalues = $(checkBoxValues[i]).find('input:radio:checked').val();
                    }
                    let radioConfigDatas = {
                        DataId: $(checkBoxValues[i]).attr('keys'),
                        DataValue: getSelectedvalues
                    }
                    vm.objData.push(radioConfigDatas);
                    let jsonObject = vm.objData.map(JSON.stringify);
                    let uniqueSet = new Set(jsonObject);
                    vm.allConfigObject = Array.from(uniqueSet).map(JSON.parse);
                }
            }
            var error = $('.is-invalid');
            let userset = [{
                refCode: 'CRMSetting',
                moduleName: 'CRM',
            }]
            var configData = "configData"
            var conditionData = "conditionData"            
            var params = "[{" + JSON.stringify(configData) + ":" + JSON.stringify(vm.allConfigObject) + ", " + JSON.stringify(conditionData) + ":" + JSON.stringify(userset) + "}]"
            if ($(error).length <= 0) {
                DataService.ManageDocUserStorageSettingData(params).then((response) => {
                    vm.isLoading = true;
                    if (response.data == 1) {
                        vm.ShowAlert(vm.$t('RecordsSavedSuccessfully'), 'success');
                        vm.isLoading = false;
                    }
                    else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                        vm.isLoading = false;
                    }
                });
            }
            else {
                vm.ShowAlert(vm.$t("PleaseCorrectHighlightedErrors"), "failure", true);
            }
        },
    }
}
</script>
