<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <div id="step1" class="tab-pane call-detail fade p-0 border pb-3 active show">
                    <section class="page-action w-100">
                        <div class="container-fluid p-0">
                            <div class="col-md-12 p-0 mt-3">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control float-left"
                                    id="ulKeysContainer">
                                    <dynamic-form lang="en" ref="AddConfig" :buttons="buttons" :schema="FormSchema"
                                        v-on:OnSubmit="onSubmit">
                                        <template v-slot:tgslot-CanViewAllCrmRecord="{ data }">
                                            <label class="switch">
                                                <input class="form-check-input rdoticketfor custom-control-input dynamic"
                                                    type="checkbox" name="Can_View_CRM_record" ref="theCheckbox">
                                                <span class="slider round">
                                                    <span class="slider-yes">Yes</span>
                                                    <span class="slider-no">No</span>
                                                </span>
                                            </label>
                                        </template>
                                    </dynamic-form>
                                    <div class="col-lg-6">
                       <div class="py-2"><small><span class="text-danger">
                                   {{ $t('MandatoryString') }}</span></small>
                       </div>
                       </div>
                                </div>
                                
                            </div>
                            
                        </div>
                    </section>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
import SearchSechma from '../../CRM/AdvancedSetup/SearchSchema';
export default {
    created() {
        var vm = this;
        vm.GetUser();
    },
    data() {
        return {
            UserData: [],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('User'),
                            name: "User",
                            value: "",
                            placeholder: this.$t('User'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": false
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('CanViewAllCrmRecord'),
                            name: "CanViewAllCrmRecord",
                            value: "Yes",
                            placeholder: "",
                            validationRules: { "required": false },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
        }
    },
    methods: {
        onSubmit: function (formfields) {
            var vm = this;
            vm.isLoading = true;
            var obj = {
                User: formfields.User,
                CanViewAllCrmRecord: formfields.CanViewAllCrmRecord
            }
            var params = JSON.stringify(obj);
            DataService.SaveConfigData(params).then(response => {
                if (response.data.result == "INSERT") {
                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Configuration')]), "success", true, vm.$t("Alert"));
                } else if (response.data.result == "UPDATE") {
                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Configuration')]), "success", true, vm.$t("Alert"));
                }
                else {
                    vm.ShowAlert(vm.$t('AddedError', [vm.$t('Configuration')]), "danger", true, vm.$t("Alert"));
                }
                SearchSechma.GetProgresSetupStatusDetail(this, "Lead");
                vm.$parent.GetProgresSetupStatusDetail();
                vm.isLoading = false;
            });
        },
        GetUser: function () {
            var vm = this;
            var param = 'moduleName=CRM_USER&type=USERS';
            DataService.GetDDLDataForAddGroup(param).then(response => {
                vm.UserData = response.data.DATA;
                vm.UserData.forEach((item, indx) => {
                    vm.FormSchema[0].Data[0].config.options.push({
                        name: item.name,
                        value: item.value
                    })
                })
                vm.$refs.AddConfig.UpdateKeyValue();
            });
        }
    }
}
</script>
