<template>
    <Section>
        <div class="modal my-popups right fade show" role="dialog" style="display: block;">
            <div class="modal-dialog mw-1100" role="document">
                <div class="modal-content">
                    <div class="modal-header mheader-user">
                        <button type="button" class="close" @click="OpenPopup"><i class="fa fa-times" aria-hidden="true"></i></button>
                        <h4 class="modal-title" id="myModalLabel">{{ $t('EnableSections') }}</h4>
                    </div>
                    <div class="modal-body">
                        <h5>Select what you want to use:</h5>
                        <!-- <div class="row mt-3 EnableSections" style="min-height:300px;">
                            <div v-for="(ParentItem,ParentIndex) in GroupName" class="col-md-6 mb-4" :value="ParentItem.enable_disable_sections_id" :key="ParentIndex">
                                <div class="conversation-date heading-border d-flex justify-content-between">
                                    <span><em class="pr-1" aria-hidden="true"></em>{{ParentItem.groupKey}}</span>
                                    <div isChecked="true">
                                        <label class="switch">
                                            <input :id='"isCheck_"+ParentItem.groupData[0].enable_disable_sections_id' v-bind:checked="ParentItem.groupData[0].is_enable==1" type="checkbox" class="form-check-input rdoticketfor custom-control-input dynamic" @change="SaveSliderButton(ParentItem.groupData[0].enable_disable_sections_id)" />
                                            <span class="slider round">
                                                <span class="slider-yes" value="1">Yes</span>
                                                <span class="slider-no" value="0">No</span>
                                            </span>
                                        </label>
                                    </div>
                                </div>
                                <div class="col-12 p-0" v-for="(ChildItem,ChildIndex) in ParentItem.groupData" :key="ChildIndex">
                                    <span class="mb-2"><i class="green fa fa-check-circle"></i> {{ChildItem.enable_disable_sections_name}}</span>
                                </div>
                            </div>
                        </div> -->
                        <div class="text-right">
                            <a id="hlkSave" v-on:click="SaveEnableDisable()" class="btn btn-success formbtn" href="javascript:;" title="Save"><i class="fa fa-save pr-2"></i>Save</a> &nbsp;
                            <a id="hlkCancel" v-on:click="closepopup()" class="btn btn-danger" href="javascript:;" title="Cancel"><i class="fa fa-close pr-2"></i>Cancel</a>
                        </div>
                    </div>
                </div>
            </div>
        </div> 
    </Section>
    </template>
    
    <script>
    import DataService from "../../../services/DataService";
    export default {
        data() {
            return {
                ModuleEnableDisableList: [],
                sortBy: '',
                sortExp: '',
                TotalRecords: 0,
                pageNumber: 1,
                pageSize: 100,
                CurrentPage: 1,
                enable: null,
                jsonData: [],
                GroupName: [],
            }
        },
        async created() {
            //debugger
            //this.FetchData();
            //this.EnableDisableButton();
        },
        methods: {
            OpenPopup() {
                var vm = this;
                vm.$parent.IsEnableDisable=false;
            },
            closepopup() {
                var vm = this;
                vm.$parent.IsEnableDisable=false;
            },
            async FetchData() {
                debugger
                var vueObj = this;
                vueObj.isLoading = true;
                var url = `moduleName=CRM&feature_id=&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&pageSize=${vueObj.pageSize}&pageNumber=${vueObj.pageNumber}`;
                await DataService.GetEnableDisableFeature(url).then(response => {
                    vueObj.isLoading = false;
                    if (response.data != null && response.data != 0) {
                        if (response.data.length > 0) {
                            vueObj.GroupName = [];
                            vueObj.ModuleEnableDisableList = response.data;
                            var gruopBy = "";
                            $.each(vueObj.ModuleEnableDisableList, $.proxy(function (index, item) {
                                gruopBy = item.group_name;
                                var chkGroup = $.grep(vueObj.GroupName, function (element, index) {
                                    return element.groupKey == gruopBy;
                                });
                                if (chkGroup.length == 0) {
                                    var returnedData = $.grep(vueObj.ModuleEnableDisableList, function (element, index) {
                                        return element.group_name == gruopBy;
                                    });
                                    if (returnedData.length > 0) {
                                        var objGroup = {
                                            groupKey: gruopBy,
                                            groupData: returnedData,
                                        };
                                        vueObj.GroupName.push(objGroup);
                                    }
                                }
                            }, this));
                            vueObj.TotalRecords = response.data[0].TOTAL;
                            vueObj.TotalPages = Math.ceil(
                                vueObj.TotalRecords / vueObj.pageSize
                            );
                            vueObj.CurrentPage = vueObj.pageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.ModuleEnableDisableList = [];
                            vueObj.TotalRecords = 0;
                        }
                    }
                })
            },
            SaveEnableDisable() {
                var vm = this;
                var params = JSON.stringify(vm.jsonData);
                DataService.SaveEnableDisableFeature(params).then(response => {
                    if (response.data == 1) {
                        vm.ShowAlert("Record Saved Successfully"), "success", ('Alert');
                        vm.closepopup();
                    } else {
                        vm.ShowAlert("No Record Saved"), "success", ('Alert');
                        vm.closepopup();
                    }
                })
            },
            SaveSliderButton: function (ss) {
                var vm = this;
                var isChecked = $("#isCheck_" + ss).is(":checked");
                const Index = vm.ModuleEnableDisableList.find(x => x.enable_disable_sections_id === ss);
                Index.is_enable = isChecked;
                var postjSON = {
                    enable_disable_sections_id: ss,
                    is_enable: Index.is_enable == true ? 1 : 0,
                };
                vm.jsonData.push(postjSON);
            },
        }
    }
    </script>
    
    <style>
    .EnableSections .green {
        color: green;
        margin-right: 5px;
    }
    
    .conversation-date .slider.round {
        border-radius: 34px;
        border: none !important;
    }
    
    .conversation-date .slider.round span {
        border: none;
    }
    </style>
    