<template>
    <div class="col-md-12 p-0">
        <div class="tab-content">
            <div id="step2" class="tab-pane active show border">
                <section class="page-action w-100">
                    <div class="container-fluid p-0">
                        <div class="col-md-12 p-0">
                            <div class="col-12 float-left p-0">
                                <div class="row">
                                    <div class="col-12">
                                        <div class="form-group projectConfig">
                                            <label>
                                                <span> Auto-Activate Client </span>
                                                <span class="text-danger">*</span>
                                                <a href="javascript:;" data-toggle="popoveruserguide"
                                                    data-content=" Auto-Activate the client at the time of Creation."
                                                    data-original-title="" title="">
                                                    <i class="fa fa-question-circle-o text-popover"
                                                        style="font-size: 14px;"></i>
                                                </a>
                                            </label>
                                            <div class="rad_value" type="radiobutton">
                                                <label class="cogfiglbl form-check form-check-inline">
                                                    <div class="custom-control custom-radio custom-control-inline">
                                                        <input type="radio" name="SendNotificationsToClient" value="Yes"
                                                            v-model="IsClientAutoActivated" id="rdo_0"
                                                            class="custom-control-input">
                                                        <label class="custom-control-label" for="rdo_0"></label>
                                                    </div>Yes
                                                </label>
                                                <label class="cogfiglbl form-check form-check-inline">
                                                    <div class="custom-control custom-radio custom-control-inline">
                                                        <input type="radio" name="SendNotificationsToClient" value="No"
                                                            v-model="IsClientAutoActivated" id="rdo_1"
                                                            class="custom-control-input">
                                                        <label class="custom-control-label" for="rdo_1">
                                                        </label>
                                                    </div>No
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-12 mt-3 p-0">
                                        <div class="col-lg-6 text-right float-right">
                                            <a class="btn btn-success formbtn" href="javascript:;" title="Save"
                                                @click="saveClientConfiguration()"><i class="fa fa-save pr-2"></i>Save</a>
                                        </div>
                                        <div class="col-lg-6">
                                            <div class="py-2"><span class="mandatory">{{$t('MandatoryString')}}</span></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../../services/DataService'
export default {
    data() {
        return {
            IsClientAutoActivated: '',
            ClientAutoActivationConfigId: null
        }
    },
    created: function () {
        this.getClientConfigValue();
    },
    methods: {
        getClientConfigValue: function () {
            var vm = this;
            var params = `moduleName=Client&keyValue=SendNotificationsToClient`;
            DataService.GetConfigDataValue(params).then((response) => {
                vm.isLoading = true;
                vm.IsClientAutoActivated = JSON.parse(response.data.ConfigValue)[0].CONFIG_DATA_VALUE;
                vm.ClientAutoActivationConfigId = JSON.parse(response.data.ConfigValue)[0].core_config_id;
                vm.isLoading = false;
            });
        },
        saveClientConfiguration: function () {
            var vm = this;
            let objStorageConfigData = [
                {
                    configData: [
                        {
                            DataId: vm.ClientAutoActivationConfigId,
                            DataValue: vm.IsClientAutoActivated == 'Yes' ? 'Yes' : 'No',
                        },
                    ]
                },

                {
                    conditionData: {
                        refCode: 'CRMSetting',
                        moduleName: 'CRM',
                    }

                }
            ];
            var jsonStorageConfigObj = JSON.stringify(objStorageConfigData);
            DataService.ManageCRMSettingData(JSON.stringify(jsonStorageConfigObj)).then((response) => {
                if (response.data == 1) {
                    this.ShowAlert("Client Configuration Data Updated Successfully");
                    this.getClientConfigValue();
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                }
            });
        },
    }
}
</script>
